*,
*::before,
*::after {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

body{
    color: white;
}

.main {
    width: 100%;
}

.left{
    display: flex;
    gap: 24px;
    align-items: center;
}

.header-list{
    display: flex;
    gap: 24px;
    list-style: none;
}

.header-button{
    display: flex;
    align-items: center;
    justify-content: center;
    gap:  4px;
    padding: 8px;
    width: 75px;
    height: 35px;
}

.main-content {
    display: flex;
    padding-top: 250px; 
    padding-left: 70px;
    padding-right: 12px;
    padding-bottom: 125px;
    gap:12px;
    background-color: var(--body-color);
    color: rgb(34, 34, 34);
}

.left-text{
    max-width: 500px;
}

.about{
    display: flex;
    background-color: var(--main-color);
    align-items: center;
    padding:120px;
    flex-direction: column;
    gap: 80px
}

.cards-container{
    display: flex;
    justify-content: space-between;
    gap:100px;
}